.about__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 65px;
}
@media (max-width: 768px) {
  .about__container {
    flex-direction: column;
    gap: 60px;
  }
}

.about__text {
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0px;
  color: #88888d;
  padding-block-end: 24px;
}

.about__body-item {
  min-width: 145px;
}

.about__title {
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-style: normal;
  font-size: 48px;
  line-height: 56px;
  letter-spacing: -1px;
  max-width: 508px;
}
@media (max-width: 768px) {
  .about__title {
    font-size: 32px;
    line-height: 40px;
    letter-spacing: -0.3px;
  }
}

.about__column--text {
  max-width: 530px;
}
@media (max-width: 1025px) {
  .about__column--text {
    max-width: 270px;
  }
}
@media (max-width: 768px) {
  .about__column--text {
    min-width: 100%;
  }
}

.about__text {
  width: 100%;
}